import React, {useState} from 'react';
import style from './users.module.css';
import Button from "../../UI/Button/Button";
import TransitionsModal from "../../UI/Modal/Modal";
import Form from "../../Form/Form";
import {useTranslation} from "react-i18next";

export default function AddUser(props) {
   const [open, setOpen] = useState(false);
   const {t} = useTranslation();

   const handleOpen = () => {
      setOpen(true);
   }
   const handleClose = () => {
      setOpen(false);
   };

   const inputForm = [
      {
         label: t('org'),
         variant: 'outlined',
         name: 'org',
         value: 'org',
         required: true
      },
      {
         label: t('position'),
         variant: 'outlined',
         name: 'position',
         value: 'position',
         required: true
      },
      {
         label: t('supplyPos'),
         variant: 'outlined',
         name: 'snabjeniye',
         value: 'snabjeniye',
         required: true
      },
      {
         label: t('userType'),
         variant: 'outlined',
         name: 'type',
         value: 'type',
         required: true
      },
      {
         label: t('surName'),
         variant: 'outlined',
         name: 'surname',
         value: 'surname',
         required: true
      },
      {
         label: t('name'),
         variant: 'outlined',
         name: 'name',
         value: 'name',
         required: true
      },
      {
         label: t('fatherName'),
         variant: 'outlined',
         name: 'middleName',
         value: 'middleName',
         required: true
      },
      {
         label: t('order'),
         variant: 'outlined',
         name: 'order',
         value: 'order',
         required: true
      },
      {
         label: t('login'),
         variant: 'outlined',
         name: 'login',
         value: 'login',
         required: true
      },
      {
         label: t('password'),
         variant: 'outlined',
         name: 'password',
         value: 'password',
         required: true
      },
      {
         label: t('mobileNum'),
         variant: 'outlined',
         name: 'number',
         value: 'number',
         required: true
      }
   ]

   return (
      <div>
         {props.img ? <img src={props.img} alt="" onClick={handleOpen}/> :
            <Button btnType="addUser" clicked={handleOpen}>{t('add')}</Button>}
         <TransitionsModal open={open} handleClose={handleClose}>
            <div className={style.modal}>
               <p>{t('addUser')}</p>
               <Form inputForm={inputForm} type={'add'} handleClose={handleClose}/>
            </div>
         </TransitionsModal>
      </div>
   )
}

